      text: `
  ### 滚动效果表格
  #### RollingDynamic基础用法
  - 使用场景：部分业务场景用到滚动效果，如大屏滚动表格，门户滚动新闻等。\`RollingDynamic\`可以满足这类需求
  - \`data\`：数据源，对象数组
  - \`v-slot:title\`：自定义标题
  - \`v-slot:list='item'\`：自定义行内容
  - \`isScroll\`：滚动开关，默认true
  - \`showNum\`：显示几行数据，默认展示\`6\`行数据
  - \`cacheLength\`：滚动数据缓存，默认\`15\`行数据
  > \`v-slot:list='item'\`插槽对应数组内对象，通过\`item.data\`取到对象数据。

  > 滚动数据缓存的使用场景：滚动内容是一个数组，而这个数据是由接口定时查询得到的最新数据，当接口返回最新数据是少量时，如2条，滚动内容只有2条，不符合期望。通过\`cacheLength\`避免这类问题，由它指定实际滚动数据，滚动内容总数据为指定数量，如默认值15

  #### 示例
  \`\`\`pug
  RollingDynamic.rolling(:data="financingData" :showNum="6" :cacheLength="15" :isScroll="true")
  \ttemplate(v-slot:title)
  \t\tp.rolling_title 融资动态
  \ttemplate(v-slot:list='item')
  \t\tp.rolling_item
  \t\t\tspan {{item.data.company}} 在
  \t\t\tspan {{item.data.date}} 申请了
  \t\t\tspan {{item.data.product}}
  \`\`\`
  \`\`\`vue
  data() {
    return {
      financingData: [
        { company: '杭州****公司1', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司2', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司3', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司4', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司5', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司6', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司7', product: "中国银行xxx产品", date: "08月10日" },
      ],
    }
  }
  mounted() {
    setTimeout(() => {
      this.financingData = [
        { company: '杭州****公司8', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司9', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司10', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司11', product: "中国银行xxx产品", date: "08月10日" },
        { company: '杭州****公司12', product: "中国银行xxx产品", date: "08月10日" },
      ]
      console.log('更新动态列表项')
    },6000)
  }
  // css
  .rolling {
    padding: 20px;
  }
  .rolling_title {
    margin-bottom: 12px;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
  }
  .rolling_item {
    text-align: left;
    font-size: 14px;
  }
  .rolling_item span {
    color: #657cc4;
  }
  \`\`\`
  - showNum < cacheLength 时，数据轮完，从头轮最大cacheLength长度的旧数据，新数据加入后直接插入立即出现
      `,
      text2: `
  #### 示例
  - showNum = cacheLength 时，数据轮完停止，直到新数据加入后继续轮
  \`\`\`
  RollingDynamic.rolling(:data="financingData" :showNum="6" :cacheLength="6")
    template(v-slot:title)
      p.rolling_title 融资动态
    template(v-slot:list='item')
      p.rolling_item <span>{{item.data.company}}</span> 在 <span>{{item.data.date}}</span> 申请了 <span>{{item.data.product}}</span>
  \`\`\`
      `,
    };
